.stepOneOfEleven {
	margin: 20px 300px 20px 20px;
}

.whatIsBothering {
	padding: 20px 0 20px 0;
}

div.paragraph p:nth-child(1) {
	text-align: justify;
	color: #a8a8a8;
}
div.paragraph p:nth-child(2) {
	text-align: justify;
	margin: 25px 0 20px 0;
}

.numbers {
	display: flex;
	margin-top: 10px;
}

div.numbers div:nth-child(1) {
	width: 3%;
}

div.numbers div:nth-child(2) {
	width: 65%;
}

textarea {
	width: 100%;
	height: 110px;
	border: 1px solid #a8a8a8;
	padding: 10px;
	border-radius: 5px;
	resize: none;
}

textarea:focus {
	border: thin solid gray;
	border-bottom: solid medium gray;
	outline: none;
	border-radius: 5px;
}
.add {
	text-align: end;
	width: 69%;
}

button.AddMore {
	background-color: white;
	border: none;
	text-align: end;
	display: inline-flex;
	margin-top: 10px;
}
div.addition {
	background-color: grey;
	border-radius: 50%;
	color: white;
	width: 20px;
	height: 20px;
	text-align: center;
}

.numbers input {
	width: 100%;
	padding: 10px;
	border-radius: 5px;
}
